    props: {
        // 列表项数据
        itemData: Object,
        // 是否简单列表
        isSimpleItem: {
            type: Boolean,
            default: false,
        }
    },
    setup: function (props) {
        return __assign(__assign({}, props), { formatDate: formatDate,
            getTopicTab: getTopicTab });
    },
